function ProductCardsItem(props) {
    return (
        <>
            <div className="prod-cards">
                <img src={props.imgsrc} alt={props.imgalttext} loading="lazy"/>
                <h4>{props.prod_name}</h4>
                <p>{props.prod_newPrice}</p>

                <div className="prod-discprice">
                    <p className="prod-oldPrice"> {props.prod_oldPrice} </p>
                    <p className="prod-discountPerc">-{props.prod_discountPerc}</p>
                </div>

                <div className="prod_btn-container">
                    <a href={props.prod_MoreDetailsLink} target="_self" rel="noreferrer nofollow" className="prod-moredetails">
                        <button>More Details</button>
                    </a>
                    <a href={props.prod_ShopeeLink} target="_blank" rel="noreferrer nofollow" className="prod-shopeelink">
                        <span aria-hidden="true" data-icon="&#xe90f;" className='shopee-icon-1'></span>
                        View in Shopee 
                    </a>
                </div>
            </div>
        </>
    );
}


