import React, { useEffect } from 'react';
import '../../App.css';
import SingleProductDetails from '../Product/SingleProduct_Details';


function SingleProductPage() {


    useEffect(() => {
        document.title = "QuickFix Product - QuickFix Most Complete Hardware Shop ";  
    }, []);

    return (
        <>
            <SingleProductDetails />
        </>
    );
}

export default SingleProductPage;