import React, {Fragment} from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
// import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';    /* react-router-dom version -> Switch (v5) = Routes (v6) */
import Navbar from './components/navigation/Navbar';
import Footer from './components/navigation/Footer';
import Home from './components/pages/Home';
import AboutUsPage from './components/pages/AboutUs_Page';      // About Us
import ContactUsPage from './components/pages/ContactUs_Page';      // Contact Us
import OnSalesPage from './components/pages/OnSales_Page';      // On Sale

import AllProductPage from './components/pages/AllProduct_Page';      // All Products
import SingleProductPage from './components/pages/SingleProduct_Page';      // Product More Details
import WaterjetPage from './components/pages/P_Waterjet_Page';
import CanopyPage from './components/pages/P_Canopy_Page';
import VacuumPage from './components/pages/P_Vacuum_Page';
import PowerTrackPage from './components/pages/P_PowerTrack_Page';

import Brands from './components/pages/Brands_Page';      // Brands
import BrandsBossmanPage from './components/pages/BrandsBossman_Page';      
import BrandsIsanoPage from './components/pages/BrandsIsano_Page';     
import BrandsZensoPage from './components/pages/BrandsZenso_Page.';      
import BrandsProguardPage from './components/pages/BrandsProguard_Page';     

import NotFoundPage from './components/pages/NotFoundPage';      // 404 page not found
import ScrollButton from './components/navigation/BackToTop';

// import ProductDetails from './components/Product/Product_Details';



function App() {

    return (
        <>
            <Router>

                <Navbar />  

                <Routes>
                    <Route exact path="/" element={<Home />} />

                    {/* products  */}
                    <Route path="/all-products" element={<AllProductPage />} />
                    <Route path="/products" element={<AllProductPage />}  />
                    <Route path="/products/:prodId" element={<SingleProductPage />}/>
                    <Route path="/products/waterjet" element={<WaterjetPage />} />
                    <Route path="/products/canopy" element={<CanopyPage />} />
                    <Route path="/products/vacuum" element={<VacuumPage />} />
                    <Route path="/products/power-track" element={<PowerTrackPage />} />

                    {/* brands  */}
                    <Route path="/brands" element={<Brands />} />
                    <Route path="/brands/bossman" element={<BrandsBossmanPage />} />
                    <Route path="/brands/isano" element={<BrandsIsanoPage />} />
                    <Route path="/brands/zenso" element={<BrandsZensoPage />} />
                    <Route path="/brands/proguard" element={<BrandsProguardPage />} />

                    <Route path="/on-sale" element={<OnSalesPage />} />
                    <Route path="/about-us" element={<AboutUsPage />} />
                    <Route path="/contact-us" element={<ContactUsPage />} />
                    <Route path="*" element={<NotFoundPage />} />

                    {/* <Route path="/test/:prodId" element={<ProductDetails />} /> */}

                </Routes>

                <Footer />
                
            </Router>

            
            <Fragment>
                <ScrollButton />
            </Fragment>
        
        </>
    );
}

export default App;
