import React, { useState } from 'react';
import { NavLink as Link } from 'react-router-dom';
import styled from 'styled-components';
import './Navbar.scss';

import Dropdown from 'react-bootstrap/Dropdown';
// import DropdownButton from 'react-bootstrap/DropdownButton';

import { Offcanvas, Accordion } from 'react-bootstrap';

export const NavLink = styled(Link)`
  &.active {
    color: #fff;
  }

`;


const Navbar = () => {

    const [click, setClick] = useState(false);
    const [setButton] = useState(true);
    // const [button, setButton] = useState(true);

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    // const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    // mobile btn
    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false);
        } else {
            setButton(true);
        }
    };

    window.addEventListener('resize', showButton);


    return (
        <>
        
            <div className="nav-contacts">
                <div>
                    <a href="mailto:quickfix.estore@gmail.com" target="_blank" rel="noreferrer nofollow">
                        <span aria-hidden="true" data-icon="&#xe901;" className='nav-icon-1'></span>
                        quickfix.estore@gmail.com 
                    </a>
                </div>
                <div>
                    <a href="/" target="_blank" rel="noreferrer nofollow">
                        <span aria-hidden="true" data-icon="&#xe902;" className='nav-icon-1'></span>
                        (+60)11 1153 0119
                    </a>
                </div>
            </div>

            <nav className="navbar">

                {/* <NavMenu > */}


                <div className="navbar-container">
                    
                    <div className="logo-container">
                        <a href='/' className="navbar-logo"  target='_parent'>
                            <img src="/qflogo4.png" alt="Quick Fix"/>
                        </a>
                    </div>

                    <div className='menu-icon' onClick={handleShow}>
                        
                        <span aria-hidden="true" data-icon="&#xe911;" className='nav-mb-icon-1'></span>
                        {/* <i className={click ? 'fas fa-times' : 'fas fa-bars'} /> */}
                    </div>
                    

                    <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                        <li className='nav-item'>
                            <NavLink to='/' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Home
                            </NavLink>
                        </li>

                        <li className='nav-item'>
                            <NavLink to='/all-products' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                All Products
                            </NavLink>
                        </li>

                        <li className='nav-item'>
                            <NavLink to='/on-sale' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                On Sale
                            </NavLink>
                        </li>

                        <li className='nav-item'>
                            {/* <NavLink to='/best-seller' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Best Seller -
                            </NavLink> */}
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    Best Seller
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/products/waterjet">Waterjet</Dropdown.Item>
                                    <Dropdown.Item href="/products/canopy">Canopy</Dropdown.Item>
                                    <Dropdown.Item href="/products/vacuum">Vacuum</Dropdown.Item>
                                    <Dropdown.Item href="/products/power-track">Power Track</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                        <li className='nav-item'>
                            {/* <NavLink to='/brands' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Brands -
                            </NavLink> */}
                            
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    Brands
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/brands/bossman">Bossman</Dropdown.Item>
                                    <Dropdown.Item href="/brands/isano">Isano</Dropdown.Item>
                                    <Dropdown.Item href="/brands/zenso">Zenso</Dropdown.Item>
                                    <Dropdown.Item href="/brands/proguard">Proguard</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                        <li className='nav-item'>
                            {/* <NavLink to='/waterjet' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Waterjet -
                            </NavLink> */}
                            
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    Waterjet
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/products/1">BPC 1070</Dropdown.Item>
                                    <Dropdown.Item href="/products/2">BPC 117</Dropdown.Item>
                                    <Dropdown.Item href="/products/3">BPC 18</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                        <li className='nav-item'>
                            {/* <NavLink to='/vacuum' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Vacuum -
                            </NavLink> */}
                            
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    Vacuum
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/products/8">20L</Dropdown.Item>
                                    <Dropdown.Item href="/products/9">17L</Dropdown.Item>
                                    <Dropdown.Item href="/products/10">12L</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                        <li className='nav-item'>
                            {/* <NavLink to='/contact-us' className='nav-links' target="_parent" onClick={closeMobileMenu} >
                                Contact Us
                            </NavLink> */}
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    Contact Us
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/about-us">About Us</Dropdown.Item>
                                    <Dropdown.Item href="/contact-us">Contact Us</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                    </ul>

                    {/* <a href="/" target="_parent" ><img src={freso_logo} className="nav-logo-mb" alt="FresoTech logo"/></a> */}

                </div> {/*  close navbar-container */}
                {/* </NavMenu> */}



                {/* Navbar mobile view  */}
                <Offcanvas show={show} onHide={handleClose} >
                    <Offcanvas.Header closeButton style={{paddingTop: "1.5rem"}}>
                        <img src="/qflogo3.png" width="160px" alt="WashLa! logo"/>
                    </Offcanvas.Header>

                    <Offcanvas.Body>
                        <div className="offcanvas-content">
                            <Link to="/" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Home</div></Link>
                            <Link to="/all-products" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">All Product</div></Link>
                            <Link to="/on-Sale" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">On Sale</div></Link>

                            <Accordion defaultActiveKey="0" flush>
                                <Accordion.Item eventKey="0">
                                    <Accordion.Header>Best Seller</Accordion.Header>
                                    <div className="offcanvas-line"></div>
                                    <Accordion.Body>
                                        <div className="offcanvas-menu-dropdown-wrapper">
                                            <div className="dropdown-wrapper-left"></div>
                                            <div className="inner-wrapper">
                                                <Link to="/products/waterjet" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Waterjet</div></Link>

                                                <Link to="/products/canopy" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Canopy</div></Link>

                                                <Link to="/products/vacuum" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Vacuum</div></Link>

                                                <Link to="/products/power-track" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Power Track</div></Link>
                                            </div>
                                        </div>
                                    </Accordion.Body>
                                </Accordion.Item>

                                <Accordion.Item eventKey="1">
                                    <Accordion.Header>Brands</Accordion.Header>
                                    <div className="offcanvas-line"></div>
                                    <Accordion.Body>
                                        <div className="offcanvas-menu-dropdown-wrapper">
                                            <div className="dropdown-wrapper-left"></div>
                                            <div className="inner-wrapper">
                                                <Link to="/brands/bossman" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Bossman</div></Link>

                                                <Link to="/brands/isano" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Isano</div></Link>

                                                <Link to="/brands/zenso" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Zenso</div></Link>

                                                <Link to="/brands/proguard" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Proguard</div></Link>
                                            </div>
                                        </div>
                                    </Accordion.Body>
                                </Accordion.Item>

                                <Accordion.Item eventKey="2">
                                    <Accordion.Header>Waterjet</Accordion.Header>
                                    <div className="offcanvas-line"></div>
                                    <Accordion.Body>
                                        <div className="offcanvas-menu-dropdown-wrapper">
                                            <div className="dropdown-wrapper-left"></div>
                                            <div className="inner-wrapper">
                                                <Link to="/products/1" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">BPC 1070</div></Link>

                                                <Link to="/products/2" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">BPC 117</div></Link>

                                                <Link to="/products/3" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">BPC 18</div></Link>
                                            </div>
                                        </div>
                                    </Accordion.Body>
                                </Accordion.Item>

                                <Accordion.Item eventKey="3">
                                    <Accordion.Header>Vacuum</Accordion.Header>
                                    <div className="offcanvas-line"></div>
                                    <Accordion.Body>
                                        <div className="offcanvas-menu-dropdown-wrapper">
                                            <div className="dropdown-wrapper-left"></div>
                                            <div className="inner-wrapper">
                                                <Link to="/" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">20L</div></Link>

                                                <Link to="/" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">17L</div></Link>

                                                <Link to="/" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">12L</div></Link>
                                            </div>
                                        </div>
                                    </Accordion.Body>
                                </Accordion.Item>

                                <Accordion.Item eventKey="4">
                                    <Accordion.Header>Contact Us</Accordion.Header>
                                    <div className="offcanvas-line"></div>
                                    <Accordion.Body>
                                        <div className="offcanvas-menu-dropdown-wrapper">
                                            <div className="dropdown-wrapper-left"></div>
                                            <div className="inner-wrapper">
                                                <Link to="/about-us" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">About Us</div></Link>

                                                <Link to="/contact-us" onClick={handleClose} target="_parent"><div className="offcanvas-menu-title">Contact Us</div></Link>
                                            </div>
                                        </div>
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                        </div>
                    </Offcanvas.Body>
                </Offcanvas>
                
            </nav>
            
        </>

    )

};

export default Navbar;
