// import React, { useState , useEffect } from 'react';
import React from 'react';
import "./Landing.scss";
import "./Brands.scss";
import "./Product/AllProduct.scss";
import { CgChevronRight } from "react-icons/cg";



// Brands page 

function Brands()  {
    
    const brands_01 = require("../images/brand1.png");      // bossman
    const brands_02 = require("../images/brand2.png");      // proguard
    const brands_03 = require("../images/brand3.png");      // isano
    const brands_04 = require("../images/brand4.png");      // zenso

    return (
        <>
            <div className="wrap-pageHero BrandsPage">
                <h1>Brands</h1>

                <div className="pageHero-breadcrumbs">
                    <p>Home</p>
                    <CgChevronRight className="pageHero-icon-1"/>
                    <p>Brands</p>
                </div>
            </div>

            <div className="wrap-brands">
                <div className="content-container ">
                    <div className="brands-p-header">
                        <h2>Brands</h2>
                        <p>Showing all 4 results</p>
                    </div>

                    <div className="brands-content">
                        <div className="brands-item-1">
                            <a href="/brands/bossman" rel="noreferrer nofollow"><img src={brands_01} alt="QuickFix's Brands BOSSMAN" /></a>
                            <a href="/brands/proguard" rel="noreferrer nofollow"><img src={brands_02} alt="QuickFix's Brands PROGUARD" /></a>
                            <a href="/brands/isano" rel="noreferrer nofollow"><img src={brands_03} alt="QuickFix's Brands ISANO" /></a>
                            <a href="/brands/zenso" rel="noreferrer nofollow"><img src={brands_04} alt="QuickFix's Brands ZENSO" /></a>
                        </div>
                    </div>
                </div>
                
            </div>
        </>
    );
        
}


export default Brands;