// import React, { useState , useEffect } from 'react';
import React from 'react';
import '../Product/Call_Product.scss';
import ProductsCardItems from "./ProductCardsItem";
import "../Landing.scss";
import "../Product/AllProduct.scss";
import { CgChevronRight } from "react-icons/cg";


// Canopy Page
class CanopyPageItems extends React.Component {

    constructor() {
        super();
        this.state = {
            products: [],
            stats: []
        }
        
    }

    async componentDidMount() {
        const resp = await fetch("https://quickfix.settlelaah.com/api/v1/products")
        const products = await resp.json()
        this.setState({products})
        this.state.products.forEach(async product => {
            const resp = await fetch(`https://quickfix.settlelaah.com/api/v1/products/${product.Slug}`)
            const data = await resp.json()

            if(data.length)
            this.setState(prevState => (
                {stats:prevState.stats.concat(data[data.length - 1])}
            ))
        })
    }


  
    render() {

            return (
                <>
                    <div className="wrap-pageHero ProductPageHero">
                        <h1>Canopy</h1>

                        <div className="pageHero-breadcrumbs">
                            <p>Home</p>
                            <CgChevronRight className="pageHero-icon-1"/>
                            <p>Canopy</p>
                        </div>
                    </div>

                    <div className="wrap-allprod">
                        <div className="allprod-container">
                            <div className="allprod-header">
                                <h2>Canopy</h2>   
                                <p>Showing all canopy product results </p>    
                            </div>

                            <div className="prod-content">
                                {
                                    this.state.stats.map(product =>
                                        <p>{product.title}</p>
                                    )
                                }
                                {/* {data.map(item => (
                                    <ProductsCardItems 
                                        prod_imgsrc={product.thumbnail}
                                        prod_imgalttext={product.title}
                                        prod_name={product.title}
                                        prod_newPrice={product.product_prices[0].promo_price}
                                        prod_oldPrice={product.product_prices[0].regular_price}
                                        prod_discountPerc={product.product_prices[0].discount_percentage + "%"}
                                        prod_MoreDetailsLink={product.id}
                                        prod_ShopeeLink={product.external_url}
                                    />
                                 ))}  */}
                            </div>
                        </div>

                    </div>
                    
                </>
        );
    }
}


export default CanopyPageItems;