import React, { useState } from "react";
import { useParams } from 'react-router-dom';
import "../Landing.scss"
import '../Product/SingleProduct_Details.scss';
import "../Product/AllProduct.scss";
import RelatedProductsSection from "./RelatedProduct_Section";

import Modal from 'react-bootstrap/Modal';


function WrapProdDetails(props) {

    const { prodId } = useParams() //params
    // api url
    const api_url = `https://quickfix.settlelaah.com/api/v1/products/${prodId}`;

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    return (
        <>
            <div className="wrap-prod-details" >
                <div className="proddetails-breadcrumbs">
                    <p>Home</p>
                    <span>/</span>
                    <p>All Products</p>
                    <span>/</span>
                    <p><strong>{props.prod_name}</strong></p>
                </div>

                <div className="proddetails-breadcrumbs-mobile">
                    <p>Home <span>/</span> All Products <span>/</span> <strong>{props.prod_name}</strong></p>
                </div>

                <div className="proddetails-container">
                    <div className="proddetails-content-1">
                        <div className="proddetails-cont-1-item-1">
                            <img src={props.prod_imgsrc} alt={props.imgalttext} onClick={handleShow}/>
                        </div>

                        <div className="proddetails-cont-1-item-2"> 
                            <h1>{props.prod_name}</h1>
                            <p>RM {props.prod_newPrice}</p>

                            <div className="proddetails-discprice">
                                <p className="proddetails-oldPrice">RM {props.prod_oldPrice} </p>
                                <p className="proddetails-discountPerc">-{props.prod_discountPerc}</p>
                            </div>
                            
                            <div className="proddetails-shortdesc">
                                <p>{props.prod_shortdesc} </p>
                                <p>{props.prod_shortdesc} </p>
                                <p>{props.prod_shortdesc} </p>
                            </div>
                            
                            <div className="proddetails_btn-container">
                                <a href={props.prod_ShopeeLink} target="_blank" rel="noreferrer nofollow" className="proddetails-shopeelink">
                                    <button>
                                        <span aria-hidden="true" data-icon="&#xe90f;" className='shopee-icon-1'></span>
                                        View in Shopee 
                                    </button>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="proddetails-content-2">
                        <div className="proddetails-cont-2-item-1">
                            <div className="proddetails-cont-2-item-1-head">
                                <button>Description</button>
                                <span></span>
                            </div>

                            <div className="proddetails-cont-2-item-1-desc">
                                <h2>Description</h2>
                                <p>{props.prod_description}</p>
                            </div>
                        </div>
                    </div>

                    <div className="proddetails-content-3">
                        <div className="proddetails-cont-3-head">
                            <h2>Related Products</h2>
                        </div>

                        <div className="proddetails-cont-3-item">
                        <RelatedProductsSection />
                        </div>
                    </div>
                    

                </div>
            </div>

            <Modal show={show} onHide={handleClose}>
                <Modal.Body>
                    <img src={props.prod_imgsrc} alt={props.imgalttext}/>
                </Modal.Body>
            </Modal>
        </>
    );
}

class SingleProductDetails extends React.Component {
    

    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            data: [],
            product_prices: []
            
        };
    }

  
    componentDidMount() {

        fetch(`https://quickfix.settlelaah.com/api/v1/products/1`)
            .then(res => res.json())
            .then((result) => {
                this.setState({
                    isLoaded: true,
                    data: result.data
                });
            },
            (error) => {
                this.setState({
                    isLoaded: true,
                    error
                });
            }
        )
        
    }
  
    render() {

        const { error, isLoaded, data } = this.state;

        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {

            return (
                <>
                    {data.map(item => (
                        <div key={item.id}>

                        <WrapProdDetails 
                            prod_imgsrc={item.thumbnail}
                            prod_imgalttext={item.title}
                            prod_name={item.title}
                            prod_newPrice={item.product_prices[0].promo_price}
                            prod_oldPrice={item.product_prices[0].regular_price}
                            prod_discountPerc={item.product_prices[0].discount_percentage + "%"}
                            prod_shortdesc="Short description here"
                            prod_ShopeeLink={item.external_url}

                            prod_description={item.description}
                        />
                        </div>
                    ))}
                    
                </>
            );
        }   

    }
}



export default SingleProductDetails;
