import React from "react";
import "../Landing.scss"
import '../Product/SingleProduct_Details.scss';
import "../Product/AllProduct.scss";

import ProductsCardItems from "./ProductCardsItem";


class RelatedProductsSection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            data: [],
            related_data: []
            
        };
    }

  
    componentDidMount() {

        // const related_url = new URL("");
        // Promise.all([1, 2, 3, 4].map(id => 
        //     fetch(`https://quickfix.settlelaah.com/api/v1/products/${id}`).then(resp => resp.json())
        //   )).then(console.log);
        
          Promise.all([8, 9, 10, 11].map(id => 
              fetch(`https://quickfix.settlelaah.com/api/v1/products/${id}`)
              .then(res => res.json())
              .then((result) => {
                  this.setState({
                      isLoaded: true,
                      related_data: result.data,
                  });
              },
              (error) => {
                  this.setState({
                      isLoaded: true,
                      error
                  });
                }
            )))

        // fetch("https://quickfix.settlelaah.com/api/v1/products")
        //     .then(res => res.json())
        //     .then((result) => {
        //         this.setState({
        //             isLoaded: true,
        //             related_data: result.data,
        //         });
        //     },
        //     (error) => {
        //         this.setState({
        //             isLoaded: true,
        //             error
        //         });
        //     }
        // )
    }
  
    render() {
        const { error, isLoaded, related_data } = this.state;

        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {

            return (
                <>
                <div className="prod-content">
                    {related_data.map(item => (
                    
                           <ProductsCardItems 
                            // related products
                            
                            prod_imgsrc={item.thumbnail}
                            prod_imgalttext={item.title}
                            prod_name={item.title}
                            prod_newPrice={item.product_prices[0].promo_price}
                            prod_oldPrice={item.product_prices[0].regular_price}
                            prod_discountPerc={item.product_prices[0].discount_percentage + "%"}
                            prod_MoreDetailsLink={item.id}
                            prod_ShopeeLink={item.external_url}
                        />
                        
                    ))}
                    </div>
                </>
            );
        }   

    }
}


export default RelatedProductsSection;