import React, { useEffect } from 'react';
import '../../App.css';
import CanopyPageItems from '../Product/P_Canopy_PageItems';


function CanopyPage() {


    useEffect(() => {
        document.title = "Canopy Products - QuickFix";  
    }, []);

    return (
        <>
            <CanopyPageItems />
        </>
    );
}

export default CanopyPage;