
// import React, { useState , useEffect } from 'react';
import React from 'react';
import ProductsCardItems from "../Product/ProductCardsItem";
import '../Product/Call_Product.scss';
import "../Landing.scss";
import "../Product/AllProduct.scss";
import { CgChevronRight } from "react-icons/cg";


class BrandsProguard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            data: [],
            data2: []
            
        };
    }
  
    componentDidMount() {
        fetch("https://quickfix.settlelaah.com/api/v1/products?page=1")
            .then(res => res.json())
            .then(
            (result) => {
                this.setState({
                    isLoaded: true,
                    data: result.data,
                });
            },
            (error) => {
                this.setState({
                    isLoaded: true,
                    error
                });
            }
        )
    }
  
    render() {
        const { error, isLoaded, data } = this.state;

        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {

            return (
                <>
                    <div className="wrap-pageHero ProductPageHero">
                        <h1>Proguard</h1>

                        <div className="pageHero-breadcrumbs">
                            <a href="/" rel="noreferrer nofollow" >Home</a>
                            <CgChevronRight className="pageHero-icon-1"/>
                            <p>Proguard</p>
                        </div>
                    </div>

                    <div className="wrap-allprod">
                        <div className="allprod-container">
                            <div className="allprod-header">
                                <h2>Proguard</h2>   
                                {/* {data.map(item => (  */}
                                    <p>Showing all Proguard products </p>    
                                {/* ))} */}
                            </div>
                            <div className="prod-content">
                                {data.map(item => (
                                    <ProductsCardItems 
                                        prod_imgsrc={item.thumbnail}
                                        prod_imgalttext={item.title}
                                        prod_name={item.title}
                                        prod_newPrice={item.product_prices[0].promo_price}
                                        prod_oldPrice={item.product_prices[0].regular_price}
                                        prod_discountPerc={item.product_prices[0].discount_percentage + "%"}
                                        prod_MoreDetailsLink={item.id}
                                        prod_ShopeeLink={item.external_url}
                                    />
                                ))}
                            </div>
                        </div>

                    </div>
                    
                </>
            );
        }   

    }
}


export default BrandsProguard;