import React, { useEffect } from 'react';
import '../../App.css';
import '../pages/NotFoundPage.scss';


function NotFoundPage() {


    useEffect(() => {
        document.title = "Page not found | QuickFix";  
    }, []);

    return (
        <>
            <div className="PageError">
                <h1>404</h1>
                <div>
                    <p>Sorry! 
                        The page you're looking for cannot be found!</p>
                    <a href="/" className='b-to-homepage-btn' rel="noreferrer">
                        Visit Home
                    </a>
                </div>
            </div>
        </>
    );
}

export default NotFoundPage;